import React from 'react';
import './CookiesConsentMobile.scss'

const CookiesConsentMobile = ({cookiesConsent,setCookiesConsent}) => {
    return (
        <div className={cookiesConsent ? "cookiesConsent__container__mobile close":"cookiesConsent__container__mobile"}>
            <h1>Dbamy o Twoj prywatno</h1>
            <div className="paragraph__container">
            <p>Nasza strona przetwarza Twoje dane osobowe zbierane w Internecie, w tym informacje zapisywane za pomoc technologii takich jak pliki cookies, sygnalizatory WWW lub innych podobnych technologii umoliwiajcych wiadczenie dopasowanych i bezpiecznych usug, personalizacj treci oraz reklamy, udostpnianie funkcji mediw spoecznociowych oraz analizowanie ruchu w Internecie.

                My i nasi partnerzy uzyskujemy dostp i przechowujemy informacje na urzdzeniu oraz przetwarzamy dane osobowe, takie jak unikalne identyfikatory i standardowe informacje wysyane przez urzdzenie czy dane przegldania w celu wyboru oraz tworzenia profilu spersonalizowanych treci i reklam, pomiaru wydajnoci treci i reklam, a take rozwijania i ulepszania produktw. Za zgod uytkownika my i nasi partnerzy moemy korzysta z precyzyjnych danych geolokalizacyjnych oraz identyfikacj poprzez skanowanie urzdze.
            </p>
            </div>
            <div className="buttons__container">
                <button onClick={()=>setCookiesConsent(true)} className="btn red">Nie, dzikuj</button>
                <button onClick={()=>setCookiesConsent(true)} className="btn green">Tak, zgadzam si</button>
            </div>
        </div>
    );
};

export default CookiesConsentMobile;